import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../auth.service';


import { ToastrService } from 'ngx-toastr';
import { ToastConfig, Toaster, ToastType } from "ngx-toast-notifications";
declare var $: any;
@Component({
	selector: 'app-login',
	templateUrl: './login.component.html',
	styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
	otpshow: any;
	loginshow: any;
	afterTime:boolean=false;

	menuShow: any; KLS: any = {};
	menuHide: any; dat: any = {}; model :any= {};
	data: any = []; otp: any; otp2: any; mobilelocal: any; objects: any = {};
	constructor(public authservice: AuthService
		, private router: Router, private toaccstr: ToastrService, private toaster: Toaster) {
		this.otpshow = false;
		this.loginshow = true;
	}

	ngOnInit() {

	
	}

	
	login() {
	
		localStorage.setItem('mobileeazy', this.model.mobile);
		this.authservice.loginCustomer(this.model)
			.subscribe(
				data => {
					this.data = data;
					console.log(data)
					if (data.status == "success") {
						let ch = data.response.id;

						localStorage.setItem('userid', ch);
						$('#myModal').modal('hide');
						this.toaccstr.success('One Time Password sent to your Registered Mobile Number', 'Success!');
						$('#myModals').modal('show');
						setTimeout(()=>{this.afterTime=true; }, 10000);
						this.otp = data.response.otp;
						console.log(this.otp);
						this.otp2 = this.otp;
						console.log(this.otp2);

						this.otpshow = true;
						this.loginshow = false;
					}

				},
				err => {
					console.log(err)
				}
			);


	}
	otpSubmit() {
		console.log(this.model.otp);
		this.otp=this.model.otp;
		this.mobilelocal = localStorage.getItem('mobileeazy');
		/*  */
		this.objects = {
			"otp": this.otp,
			"mobile": this.mobilelocal
		}
		console.log(this.objects);
		this.authservice.loginCustomer(this.objects)
			.subscribe(
				data => {
					this.data = data;

					if (data.status == "success") {
						this.toaccstr.success('Login Successfully!', 'Success!');
						$('#myModals').modal('hide');
						localStorage.setItem('authKey', data.response.authkey);

						if (data.response.firstName || data.response.lastName) {


							this.router.navigate(["home"])
						}
						if (!data.response.firstName) {

							this.router.navigate(["Registration"])
						}
						this.menuShow = true;
						this.menuHide = false;
					}

				},
				err => {
					console.log(err)
				}
			);
	}
	resend() {
		console.log('lll');

		this.KLS.mobile = localStorage.getItem('mobileeazy');
		this.model = this.KLS;
		this.login();
	}
}
