import { Component, OnInit } from '@angular/core';
import{Subject} from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';
import { Router , Routes } from '@angular/router';
import { FormBuilder, FormGroup, Validators,FormControl } from '@angular/forms';
import { AuthService } from '../../auth.service';
import { ViewChild } from '@angular/core';
import { DataTablesModule } from 'angular-datatables';
@Component({
  selector: 'app-jobsdata',
  templateUrl: './jobsdata.component.html',
  styleUrls: ['./jobsdata.component.css']
})
export class JobsdataComponent implements OnInit {
 @ViewChild('f') form: any;
model: any = {};sizes:any;mode:any;genders:any=[];
	modes:any=[];cityName:any;evt:any;servicy:any={};applications:any={};apply:boolean=false;
public jobs:any=[];jobmodal:any=[];modaljobs:any=[];savedjobs:any=[];Approved:any=[];Closed:any=[];Cancelled:any=[];Selected:any=[];Pending:any=[];
  dtOptions: DataTables.Settings = {}; 
  dtTrigger: Subject<any> =new Subject;
  constructor(public authservice:AuthService ,public router:Router) {this.genders=["Male","Female"];
	this.createjoblimitmode(); }

  ngOnInit() {  this.jobslist();
	   this.dtOptions = {
      pagingType: 'full_numbers',pageLength:10
  }
  }
createjoblimitmode(){
 
	this.authservice.createjoblimitmode()
	 .subscribe((data : any)=>{
	/* 	 console.log(data); */
	this.modes=data.response;
/* 	console.log(this.modes)
    console.log(this.genders) */
    },
	err =>{
		console.log(err)
		}
    );
	}
	onSubmit() {
		/*    alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.model)) */
	var ch= {
		"customer":{
		"id":localStorage.userid
	  },
	  "title":this.model.jobtitle,
		"description":this.model.jobdescription,
		"address":this.model.jobaddress,
		"gender":this.model.gender,
		"reqMembers":this.model.jobreqmem,
		"limits":this.model.jobduration,
		"limitMode":this.model.durationmode,
		"amount":this.model.payingperhead,
		"from_date":this.model.FromDate,
		"to_date":this.model.ToDate,
		"start_time":this.model.fromtime,
		"end_time":this.model.Totime
	  }
		 console.log(ch)
		/* 	  alert(ch); */
	
		this.authservice.createjobs(ch)
		.subscribe((data : any)=>{
	/* 	 console.log(data); */
		this.sizes=data.response;
		if(data.status=="success"){
			alert("job created successfully and your job id is"+data.response);
			this.form.reset();
		//this.router.navigate(["jobslist"])
		this.router.navigate(['jobsdata/Joblist']); 
		}
    
		},
		err =>{
				console.log(err)
			}
		); 
	
		}
  jobslisty(){
	  this.savedjobs=[];this.Approved=[];this.Closed=[];this.Cancelled=[];
	  this.jobslist();
  }
  savedclose(ch){
	  this.servicy=ch;
	  	this.servicy.serv="Closed";
	   this.authservice.jobcloscancel(this.servicy)
	 .subscribe((data : any)=>{
		/*  console.log(data); */
  this.jobslisty();
	
    
    },
	err =>{
				console.log(err)
			}
    ); 
  }
   savedcancel(ch){
	   this.servicy=ch;
	  	this.servicy.serv="Cancelled";
	   this.authservice.jobcloscancel(this.servicy)
	 .subscribe((data : any)=>{
	/* 	 console.log(data); */
  this.jobslisty();
	
    
    },
	err =>{
				console.log(err)
			}
    );
	  
	  
  }
  
  
  
  
     approvedselectlist(ch){
		 this.Pending=[];
		 this.Selected=[];
	   this.servicy=ch;
	  
	   this.authservice.approvedselectlist(this.servicy)
	 .subscribe((data : any)=>{
		/*  console.log(data); */
		  /*  this.savedjobs=[];this.Approved=[];this.Closed=[];this.Cancelled=[];
  this.jobslist(); */
 this.apply=true;
	 this.applications=data.response;
	/*  console.log(this.applications.length) */
	  for(let i=0;i<this.applications.length;i++){
			if(this.applications[i].status=="Selected_By_Customer"){
				 this.Selected.push(this.applications[i]);
				 
			 }
			 else  if(this.applications[i].status=="Applied"){
				 this.Pending.push(this.applications[i]);
			
			 }
			/*  console.log( this.Selected);
	  console.log( this.Pending); */}
    
    },
	err =>{
				console.log(err);
			}
    )
	 
	  
  }
  
  selectapprovedapplicant(p){
/* 	  console.log(p); */
	     this.servicy=p;
	  	this.servicy.serv="Selected_By_Customer";
		
		
		 this.authservice.selectapplicantsforapproved(this.servicy)
	 .subscribe((data : any)=>{
		/*  console.log(data); */
	
    },
	err =>{
				console.log(err)
			}
    );
		
		
		
		
		
		
		
  }
  jobslist(){
 this.authservice.CustomerJobslist()
	 .subscribe((data : any)=>{
	/* 	 console.log(data); */
		 this.jobs=data.response;
	/* 	 console.log(this.jobs.length) */
		 for(let i=0;i<this.jobs.length;i++){
			 if(this.jobs[i].status=="Saved"){
				 this.savedjobs.push(this.jobs[i]);
			
			 }
			 else if(this.jobs[i].status=="Approved"){
				 this.Approved.push(this.jobs[i]);
				 
			 } else if(this.jobs[i].status=="Closed"){
				 this.Closed.push(this.jobs[i]);
				 
			 }
			  else if(this.jobs[i].status=="Cancelled"){
				 this.Cancelled.push(this.jobs[i]);
				 
			 }
			
		 }
		  this.dtTrigger.next();
			/*  console.log(this.savedjobs);
			 console.log(this.Approved);
			 
			 console.log(this.Closed);
			 console.log(this.Cancelled); */


	
    
    },
  err =>{
				console.log(err)
			}); }
			modal(id){
			
				 this.Pending=[];
				 this.Selected=[];
			/* 	console.log(id); */
				this.modaljobs=id;
				
				/* console.log(this.modaljobs.status); */
					this.apply=false;
			}



}
