import { Component, OnInit } from '@angular/core';
import{Subject} from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';

import { DataTablesModule } from 'angular-datatables';
import { AuthService } from '../../auth.service';
@Component({
  selector: 'app-joblist',
  templateUrl: './joblist.component.html',
  styleUrls: ['./joblist.component.css']
})
export class JoblistComponent implements OnInit {
	cityName:any;evt:any;servicy:any={};applications:any={};apply:boolean=false;
public jobs:any=[];jobmodal:any=[];modaljobs:any=[];savedjobs:any=[];Approved:any=[];Closed:any=[];Cancelled:any=[];Selected:any=[];Pending:any=[];
  dtOptions: DataTables.Settings = {}; 
  dtTrigger: Subject<any> =new Subject;
  constructor(public authservice:AuthService) { 
  
}

  ngOnInit() {
	  this.jobslist();
	   this.dtOptions = {
      pagingType: 'full_numbers',pageLength:10
  }}
  jobslisty(){
	  this.savedjobs=[];this.Approved=[];this.Closed=[];this.Cancelled=[];
	  this.jobslist();
  }
  savedclose(ch){
	  this.servicy=ch;
	  	this.servicy.serv="Closed";
	   this.authservice.jobcloscancel(this.servicy)
	 .subscribe((data : any)=>{
		/*  console.log(data); */
  this.jobslisty();
	
    
    },
	err =>{
				console.log(err)
			}
    ); 
  }
   savedcancel(ch){
	   this.servicy=ch;
	  	this.servicy.serv="Cancelled";
	   this.authservice.jobcloscancel(this.servicy)
	 .subscribe((data : any)=>{
	/* 	 console.log(data); */
  this.jobslisty();
	
    
    },
	err =>{
				console.log(err)
			}
    );
	  
	  
  }
  
  
  
  
     approvedselectlist(ch){
		 this.Pending=[];
		 this.Selected=[];
	   this.servicy=ch;
	  
	   this.authservice.approvedselectlist(this.servicy)
	 .subscribe((data : any)=>{
		/*  console.log(data); */
		  /*  this.savedjobs=[];this.Approved=[];this.Closed=[];this.Cancelled=[];
  this.jobslist(); */
 this.apply=true;
	 this.applications=data.response;
	/*  console.log(this.applications.length) */
	  for(let i=0;i<this.applications.length;i++){
			if(this.applications[i].status=="Selected_By_Customer"){
				 this.Selected.push(this.applications[i]);
				 
			 }
			 else  if(this.applications[i].status=="Applied"){
				 this.Pending.push(this.applications[i]);
			
			 }
			/*  console.log( this.Selected);
	  console.log( this.Pending); */}
    
    },
	err =>{
				console.log(err);
			}
    )
	 
	  
  }
  
  selectapprovedapplicant(p){
/* 	  console.log(p); */
	     this.servicy=p;
	  	this.servicy.serv="Selected_By_Customer";
		
		
		 this.authservice.selectapplicantsforapproved(this.servicy)
	 .subscribe((data : any)=>{
		/*  console.log(data); */
	
    },
	err =>{
				console.log(err)
			}
    );
		
		
		
		
		
		
		
  }
  jobslist(){
 this.authservice.CustomerJobslist()
	 .subscribe((data : any)=>{
	/* 	 console.log(data); */
		 this.jobs=data.response;
	/* 	 console.log(this.jobs.length) */
		 for(let i=0;i<this.jobs.length;i++){
			 if(this.jobs[i].status=="Saved"){
				 this.savedjobs.push(this.jobs[i]);
			
			 }
			 else if(this.jobs[i].status=="Approved"){
				 this.Approved.push(this.jobs[i]);
				 
			 } else if(this.jobs[i].status=="Closed"){
				 this.Closed.push(this.jobs[i]);
				 
			 }
			  else if(this.jobs[i].status=="Cancelled"){
				 this.Cancelled.push(this.jobs[i]);
				 
			 }
			
		 }
		  this.dtTrigger.next();
			/*  console.log(this.savedjobs);
			 console.log(this.Approved);
			 
			 console.log(this.Closed);
			 console.log(this.Cancelled); */


	
    
    },
  err =>{
				console.log(err)
			}); }
			modal(id){
			
				 this.Pending=[];
				 this.Selected=[];
			/* 	console.log(id); */
				this.modaljobs=id;
				
				/* console.log(this.modaljobs.status); */
					this.apply=false;
			}


}


$('.scroll_table').wrap('<div class="dataTables_scroll"/>');

