import { Component, OnInit } from '@angular/core';

import { Router , Routes } from '@angular/router'; 
declare var $: any;
import { AuthService } from '../auth.service';
@Component({
	selector: 'app-stotecreate',
	templateUrl: './stotecreate.component.html',
	styleUrls: ['./stotecreate.component.css']
})
export class StotecreateComponent implements OnInit {
data:any;
	constructor(public authservice: AuthService,public router:Router) { }

	ngOnInit() {
	}
	Store(StoreData) {
		// /  this.address=	StoreData.address ; /
		console.log(StoreData);
		this.authservice.StoreCreate(StoreData)
			.subscribe(
				data => {
					this.data = data;
					console.log(data)
					if (data.status == "success") {
						// / console.log('lllll'); /
						alert('created successfully');
						$('#Store').modal('hide');
						this.router.navigate(["home"]);
					}

				},
				err => {
					console.log(err)
				}
			);
	}
}
