import { Component, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { AuthService } from '../auth.service';
import { FormBuilder, FormGroup, Validators,FormControl } from '@angular/forms';
import { DatePipe } from '@angular/common';
@Component({
	selector: 'app-shipslist',
	templateUrl: './shipslist.component.html',
	styleUrls: ['./shipslist.component.css']
})
export class ShipslistComponent implements OnInit {
	shippingsItems: any = []; mo: any = {}; shippslist: any = {}; itemm: any; tracklist: any; dir: any = {};
	url: any; cityName: any; evt: any; servicy: any = {}; applications: any = {}; apply: boolean = false; itemsslist: any; shipping: any = {}; ispaying: any; driversShipps: any; shippId: any; itemDocs: any;
	public jobs: any = []; jobmodal: any = []; modaljobs: any = []; savedjobs: any = []; Approved: any = []; Closed: any = []; Cancelled: any = []; Selected: any = []; Pending: any = [];
	dtOptions: DataTables.Settings = {};
	dtTrigger: Subject<any> = new Subject;
	 registerForm: FormGroup;
    submitted = false;
	value1:any;
	value2:any;
	dates:any;
	date:any;
	constructor(public authservice: AuthService,private formBuilder: FormBuilder,public datePipe:DatePipe) {
		this.url = this.authservice.Customer
		console.log(this.url)
	}

	ngOnInit() {
		this.jobslist();
		this.dtOptions = {
			pagingType: 'full_numbers', pageLength: 10
		}
		 this.registerForm = this.formBuilder.group({
            formDate: ['', Validators.required],
            toDate: ['', Validators.required]
           
        });
	}


	CustomerShippingslist() {
		this.Selected = [];
		this.authservice.CustomerShippingslist()
			.subscribe((data: any) => {
				console.log(data);

				this.shippslist = data.response;

				for (let i = 0; i < this.shippslist.length; i++) {

					this.itemm = this.shippslist[i];


					this.authservice.itemslist(this.itemm.id)
						.subscribe((data: any) => {
							console.log(data);
							this.Selected.push(data.response);


						},
							err => {
								console.log(err)
							});







				}

			},
				err => {
					console.log(err)
				});
	}



	retry(mo) {



		this.authservice.retry(mo)
			.subscribe((data: any) => {

				if (data.status == "success") {

					console.log(data);


					this.itemslist(mo);
					this.CustomerShippingslist();


				}
			},
				err => {
					console.log(err)
				});




	}
	modals(ll) {

		this.mo = ll;
		console.log(this.mo.id);
		console.log(this.mo.id);
		this.itemslist(this.mo.id);
	}

	itemslist(ll) {
		console.log(ll)
		this.authservice.itemslist(ll)
			.subscribe((data: any) => {
				console.log(data);
				this.itemsslist = data.response;
				console.log(this.itemsslist);

				this.shipping = data.response.shipping;
				this.ispaying = this.shipping.payingAt;

				this.driversShipps = data.response.driversShipps;

				this.shippId = this.shipping.shippId;
				this.itemDocs = data.response.itemDocs;
				this.shippingsItems = data.response.shippingsItems;


			},
				err => {
					console.log(err)
				});
	}
	jobslisty() {
		this.savedjobs = []; this.Approved = []; this.Closed = []; this.Cancelled = [];
		this.jobslist();
	}
	savedclose(ch) {
		this.servicy = ch;
		this.servicy.serv = "Closed";
		this.authservice.jobcloscancel(this.servicy)
			.subscribe((data: any) => {

				this.jobslisty();


			},
				err => {
					console.log(err)
				}
			);
	}
	savedcancel(ch) {
		this.servicy = ch;
		this.servicy.serv = "Cancelled";
		this.authservice.jobcloscancel(this.servicy)
			.subscribe((data: any) => {

				this.jobslisty();


			},
				err => {
					console.log(err)
				}
			);


	}




	approvedselectlist(ch) {
		this.Pending = [];
		this.Selected = [];
		this.servicy = ch;

		this.authservice.approvedselectlist(this.servicy)
			.subscribe((data: any) => {

				this.apply = true;
				this.applications = data.response;

				for (let i = 0; i < this.applications.length; i++) {
					if (this.applications[i].status == "Selected_By_Customer") {
						this.Selected.push(this.applications[i]);

					}
					else if (this.applications[i].status == "Applied") {
						this.Pending.push(this.applications[i]);

					}
				}

			},
				err => {
					console.log(err);
				}
			)


	}

	selectapprovedapplicant(p) {

		this.servicy = p;
		this.servicy.serv = "Selected_By_Customer";


		this.authservice.selectapplicantsforapproved(this.servicy)
			.subscribe((data: any) => {


			},
				err => {
					console.log(err)
				}
			);







	}

	track(llk) {

		this.tracklist = llk;


		this.dir = {
			origin: { lat: 17.3850, lng: 78.4867 },
			destination: { lat: 16.5062, lng: 80.6480 }
		}



		console.log(this.dir);
	}
	jobslist() {
		this.authservice.CustomerShippingslist() 
			.subscribe((data: any) => {
				console.log(data);
				this.savedjobs = data.response;

				this.dtTrigger.next();



			},
				err => {
					console.log(err)
				});
	}
	modal(id) {

		this.Pending = [];
		this.Selected = [];

		this.modaljobs = id;


		this.apply = false;
	}
	get f() { return this.registerForm.controls; }

    onSubmit(date) {
		console.log(date);
		this.date = date;
		var datePipe = new DatePipe("en-US");
        this.value1 = datePipe.transform(date.formDate, 'dd-MM-yyyy');
         console.log(this.value1);
		 var datePipe = new DatePipe("en-US");
        this.value2 = datePipe.transform(date.toDate, 'dd-MM-yyyy');
		console.log(this.value2);
		this.dates = {
			 "formDate":this.value1,
			 "toDate":this.value2
		}
		console.log(this.dates);
        this.submitted = true;

        // stop here if form is invalid
        if (this.registerForm.invalid) {
            return;
        }
		this.authservice.dateWiseShippingList(this.dates) 
			.subscribe((data: any) => {
				console.log(data);
				this.date ={};
				this.savedjobs = data.response;
               
				this.dtTrigger.next();



			},
				err => {
					console.log(err)
				});
        
       
    }


}



