import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import { Router , Routes } from '@angular/router';
import { FormBuilder,FormsModule, FormGroup, FormArray, FormControl, ReactiveFormsModule, Validators  } from '@angular/forms';
import { AppComponent } from '../app.component';
@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
	userForm:FormGroup
	data:any;chai:any=[];
	detailss={};
	vgh:boolean=false;
	gh:boolean=true;
	details: any = {};
	url:any = "../assets/max-rehkopf.png";
photoid:any;ch :any={}; email:any=[];firstName:any;lastName:any;
  constructor(public authservice:AuthService) { 
  	 
		this.ProfileData();
		var ch = JSON.parse(localStorage.getItem('userid'));

		this.url =	this.authservice.Customer+"app/customer/"+ch+"/profilePic";
		
	
		this.userForm=new FormGroup({
				
				'addresses' :new FormArray([
				this.createaddress()
				])
			})
  
	}
	ngOnInit() {}




	onSubmit() {
	alert( JSON.stringify(this.details))  ;
	this.details.dob="31-12-2010"

	
		this.authservice.updateprofile(this.details)
		.subscribe((data : any)=>{

		  this.ProfileData();
		
    
		},
		err =>{
				console.log(err)
			}
		); 
	
		}
ProfileData(){
       
		this.authservice.CustomerProfileData()
		.subscribe(
		    data => {
		    this.data = data;
			 	console.log(data) ;
				if(data.status == "success" ){
					this.details=data.response;
					this.detailss=data.response.firstName;
					console.log(this.detailss); 
					this.userForm.value.addresses[0].firstName=this.detailss;
				console.log(this.userForm.value.addresses[0].firstName);
				}
			
			},
			err =>{
				console.log(err)
			}
		); 
    }
	/* Profile(){
       
		this.authservice.profilepic()
		.subscribe(
		    data => {
		    this.data = data;
				console.log(data)
				
			
			},
			err =>{
				console.log(err)
			}
		); 
    } */
	
	 removeaddress(index){
	  alert(index);
	  let addres = this.userForm.get('addresses') as FormArray;
addres.removeAt(index);
  }
  createaddress(){
	  return  new FormGroup({
			  'firstName':new FormControl(),
			    'ItemQuantity':new FormControl(),
				  'width':new FormControl(),
				   'height':new FormControl(),
			       'imagename':new FormControl()
		  })
		   
	  }
  addaddress(){
	let addressarray = this.userForm.get('addresses') as FormArray;
	 addressarray.push(
	   this.createaddress()
	  )


  } 
edit(){
		this.vgh=true;
		this.gh=false;
		
	}

	browseImage() {
		$("#openWindow").click();
	}

	onSelectFile(event) {
		if (event.target.files && event.target.files[0]) {
			var reader = new FileReader();

			reader.readAsDataURL(event.target.files[0]); // read file as data url

			reader.onload = (event) => { // called once readAsDataURL is completed
				console.log(event);
				 this.url = reader.result;

				 this.authservice.uploadProfilePic(this.url).subscribe(
					data => {
					this.data = data;
					 console.log(data) ;
					if(data.status == "success" ){
						alert(data.response);
					
					}
				
				},
				err =>{
					console.log(err)
				}
			);

			}
		}
	}
}
