import { NgModule } from '@angular/core';

import { LoginComponent } from './login/login.component';
import { Routes,RouterModule } from '@angular/router';
import {HomeComponent} from './home/home.component';
import { ShippingComponent } from './shipping/shipping.component';
import { JobcreateComponent } from './modulewise/jobcreate/jobcreate.component';
import {StorecreateComponent } from './storecreate/storecreate.component';
import { MyshippingsComponent } from './myshippings/myshippings.component';
import { ShipcreateComponent } from './shipcreate/shipcreate.component';
import { ShipslistComponent } from './shipslist/shipslist.component';
import { ShippingdataComponent } from './modulewise/shippingdata/shippingdata.component';
import { JobsdataComponent } from './modulewise/jobsdata/jobsdata.component';
import { JoblistComponent } from './modulewise/joblist/joblist.component';

import { ShippchangeComponent } from './shippchange/shippchange.component';
import { RegistrationComponent } from './registration/registration.component';
import { ProfileComponent } from './profile/profile.component';


import { NotificationsComponent } from './notifications/notifications.component';
import { SupportComponent } from './support/support.component';
import { AboutComponent } from './about/about.component';
import { InviteComponent } from './invite/invite.component';

import { SuccessComponent } from './success/success.component';

import { FailComponent } from './fail/fail.component';
const routes :Routes =[
  {path:'',redirectTo:'login',pathMatch:'full'},
  {path:'login',component:LoginComponent},
  {path:'home',component:HomeComponent},
  {path:'shipping',component:ShippingComponent},
  {path:'JobcreateComponent',component:JobcreateComponent},
  {path:'store',component:StorecreateComponent},
  {path:'myshippings',component:MyshippingsComponent},
  {path:'Registration',component:RegistrationComponent},
    {path:'shippingdata',component:ShippingdataComponent},
  {path:'jobsdata',component:JobsdataComponent},
  {path:'joblist',component:JoblistComponent},
  {path:'shipslist',component:ShipslistComponent},
   {path:'shipcreate',component:ShipcreateComponent},
   {path:'profile',component:ProfileComponent},
      {path:'Shippchange',component:ShippchangeComponent},

      {path:'notifications',component:NotificationsComponent},
      {path:'support',component:SupportComponent},
      {path:'about',component:AboutComponent},
      {path:'invite',component:InviteComponent},
      {path:'Shipping_Payment_Success/:term',component:SuccessComponent},
      {path:'Shipping_Payment_Failed',component:FailComponent},

  /* children:[{path:'',redirectTo:'ShippingComponent',pathMatch:'full'},
  {path:'ShippingComponent',component:ShippingComponent},] */
 /* ,children:[{path:'',redirectTo:'JobcreateComponent',pathMatch:'full'},
  {path:'JobcreateComponent',component:JobcreateComponent},{path:'Joblist',component:JoblistComponent}] */
  /* {path:'login',component:LoginComponent},	
  {path:'home',component:HomeComponent},
  {path:'itemcreation', component:ItemcreationComponent},
  {path:'**',component:PageNotFoundComponent} */
  
  
    
  ];
  @NgModule({
    imports: [
      /* CommonModule */
    RouterModule.forRoot(routes)
    ],
    exports:[RouterModule],
    declarations: []
  })
  export class AppRoutingModule { }
