import { Component, OnInit } from '@angular/core';
import { ElementRef, NgZone, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ShippingComponent } from '../../../shipping/shipping.component'

import { HttpErrorResponse } from '@angular/common/http';
import { AuthService } from '../../../auth.service';
import { Subject } from 'rxjs';
import { ToastrService } from 'ngx-toastr';
import { ToastConfig, Toaster, ToastType } from "ngx-toast-notifications";
//import { ShippingdetailsComponent } from '../shippingdetails/shippingdetails.component';
import { MapsAPILoader } from '@agm/core';
@Component({
  selector: 'app-tolocation',
  templateUrl: './tolocation.component.html',
  styleUrls: ['./tolocation.css']
})
export class TolocationComponent implements OnInit {
  @ViewChild("search")
  public searchElementRef: ElementRef;
  startone: boolean = true;
  longitude: any;
  latitude: any; secondone: boolean = false;
  public searchControl: FormControl;

  public zoom: number;
  responseAdrress: any;
  address: any;
  public lat: Number;
  public lng: Number;
  /* public zoom: Number = 14; */

  public dir = undefined;
  constructor(private mapsAPILoader: MapsAPILoader, public authservice: AuthService, private toaccstr: ToastrService, private toaster: Toaster,
    private ngZone: NgZone, public ShippingComponent: ShippingComponent) {
    this.responseAdrress = '';

    this.address = localStorage.getItem('tofarelocation');
    //alert(this.address);
    if (this.address == null) {
      this.getViewData()
      // this.getDirection() ; 


    } else {
      this.responseAdrress = this.address;
    }





  } public origin: {}
  public destination: {}
  ngOnInit() { }
  getViewData() {

    localStorage.setItem('tolat', '17.00');
    localStorage.setItem('tolng', '81.00');
    /*   this.zoom = 4; */
    /*   this.latitude = number;
    this.longitude = number;  */

    //create search FormControl
    this.searchControl = new FormControl();
    //this.searcControl = new FormControl();
    //set current position
    this.setCurrentPosition();

    //load Places Autocomplete
    this.mapsAPILoader.load().then(() => {
      let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
        types: ["address"]
      });
      autocomplete.addListener("place_changed", () => {
        this.ngZone.run(() => {
          //get the place result
          let place: google.maps.places.PlaceResult = autocomplete.getPlace();
          console.log(place)
          localStorage.setItem('tolocation', place.formatted_address);
          //verify result
          if (place.geometry === undefined || place.geometry === null) {
            return;
          }

          //set latitude, longitude and zoom
          this.latitude = place.geometry.location.lat();
          console.log(this.latitude);
          this.longitude = place.geometry.location.lng();
          console.log(this.longitude);
          this.authservice.locationValidator(this.latitude, this.longitude)
            .subscribe((data: any) => {
              console.log(data);
              console.log(data);
              localStorage.setItem('tolat', this.latitude);
              /*   this.fromlat=this.latitude; */

              localStorage.setItem('tolng', this.longitude);
              /*  this.fromlong=this.longiitude; */
              this.zoom = 12;
              var ch = JSON.parse(localStorage.getItem('fromlat'));
              /*  console.log(ch); */
              var chh = JSON.parse(localStorage.getItem('fromlng'));
              /*  console.log(chh); */
              this.dir = {
                origin: { lat: ch, lng: chh },
                destination: { lat: this.latitude, lng: this.longitude }
              }
              /*    console.log(this.dir); */
              localStorage.setItem('dir', JSON.stringify(this.dir));
              /*  console.log(this.dir); */
              this.ShippingComponent.calculateAndDisplayRoute();

            },
              (err: HttpErrorResponse) => {
                console.log(err);
                this.responseAdrress = '';
                if (err.status == 423) {
                  this.toaccstr.error('You are not in my Boundaries!', 'Blocked!');

                }
                this.responseAdrress = '';
              });



          //public ShippingdetailsComponent:ShippingdetailsComponent,


        }
        );
      });
    });

    /*   console.log( this.longitude); */



  }
  setCurrentPosition() {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.latitude = position.coords.latitude;
        this.longitude = position.coords.longitude;
        this.zoom = 12;
      });
    }
  }
  // getDirection() {

  //   this.dir = {
  //     origin: { lat: 18.888, lng: 78.979021 },
  //     destination: { lat: 16.799524, lng: 80.975017 }
  //   }
  // 
  // }
  CurrentLocation() {
    this.responseAdrress = '';
    this.getLocation();

  }

  getLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position: Position) => {
        if (position) {
          console.log("Latitude: " + position.coords.latitude +
            "Longitude: " + position.coords.longitude);
          this.lat = position.coords.latitude;
          this.lng = position.coords.longitude;
          console.log(this.lat);
          console.log(this.lng);

          this.authservice.locationValidator(this.latitude, this.longitude)
            .subscribe((data: any) => {
              console.log(data);
              console.log(data);
              this.locationaddress();

            },
              (err: HttpErrorResponse) => {
                console.log(err);
                this.responseAdrress = '';
                if (err.status == 423) {

                  this.toaccstr.error('You are not in my Boundaries!', 'Blocked!');
                  this.responseAdrress = '';
                }
              });

          // this.toaster.open({
          //   text: this.latitude,
          //   caption: ' notification',
          //   type: 'success',
          // });



        }
      },
        (error: PositionError) => console.log(error));
    } else {
      console.log("Geolocation is not supported by this browser.");
    }
  }

  locationaddress() {
    console.log('entered to location addres')
    this.toaccstr.success('Hello world!', 'Toadstr fun!');

    console.log(this.lat);
    console.log(this.lng);
    this.latitude = this.lat;
    this.longitude = this.lng;
    this.authservice.GetAddressfor(this.latitude, this.longitude)
      .subscribe((data: any) => {
        console.log(data);
        console.log(data.results[0].formatted_address);
        console.log('get service all data');

        this.responseAdrress = data.results[0].formatted_address;



        localStorage.setItem('tolat', this.latitude);


        localStorage.setItem('tolng', this.longitude);

        var ch = JSON.parse(localStorage.getItem('fromlat'));

        var chh = JSON.parse(localStorage.getItem('fromlng'));

        this.dir = {
          origin: { lat: ch, lng: chh },
          destination: { lat: this.latitude, lng: this.longitude }
        }
        console.log(this.dir);
        localStorage.setItem('dir', JSON.stringify(this.dir));
        console.log(this.dir);
        this.ShippingComponent.ToLocationBasedCall();










      },
        (err: HttpErrorResponse) => {
          console.log(err);
        });



    //for reference check the below link
    //chaitanya reddy
    //https://developers.google.com/maps/documentation/javascript/examples/geocoding-reverse
    //      this.gecode= new google.maps.Geocoder;
    //      let latlng = {lat: parseFloat(this.lat), lng: parseFloat(this.lng)};
    //      this.gecode.geocode({'location': latlng},(results, status) => {

    // console.log(status);
    // alert('this is results');
    // alert(results);
    //         if (status === 'OK') {
    //           alert('ok ok ok')
    //           if (results[0]) {

    ////             alert(results[0].formatted_address)
    //             this.Geolocationaddress=results[0].formatted_address;
    //             alert(  this.Geolocationaddress);


    //           } else {
    //            alert('No results found');
    //           }
    //           }
    //           else {
    //             alert('Geocoder failed due to: ' + status);
    //           }
    //           this.originalFrom=false;
    //           this.StoreFrom=false;
    //           this.Geolocationdata=true;

    //       });

    this.startone = false;
    this.secondone = true;

  }
  UserEntredLocation() {
    this.responseAdrress = '';
    this.startone = true;
    this.secondone = false;


  }



}

